import React, { useEffect, useState } from 'react'
import personService from './services/persons'


const App = () => {
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filter, setFilter ] = useState('')

  const [ persons, setPersons] = useState([])
  const [ notification, setNotification ] = useState(null)
  

  const showNotification = (message, isWarning=false) => {
    console.log('Notification: ', message)

    setNotification({
      message: (message || 'unknown error'),
      isWarning
    })
    setTimeout(() => {
      setNotification(null)
    }, 5000)

  }

  const hook = () => {
      personService
      .getAll()
      .then(persons => {
        console.log('Persons received:', persons)
        setPersons(persons)
      })
  }
  useEffect(hook, [])

  const addPerson = (event) => {
    event.preventDefault()

    if (newName.length <= 0) {
      alert('"Name" field is required!')
      return;
    }

    if (newNumber.length <= 0) {
      alert('"Number" field is required!')
      return;
    }

    if (!/^[0-9+-]*$/.test(newNumber)) {
      alert('Invalid phone number!')
      return;
    }

    const newPerson = {
      name: newName,
      number: newNumber
    }

    const duplicate = persons.find(person =>
      person.name === newPerson.name
    )
    
    if (duplicate) {
      if (window.confirm(`${newPerson.name} already exists, replace the old number with a new one?`))
      {
        personService
        .update(duplicate.id, {...duplicate, number: newPerson.number}) //could be just newPerson i guess
        .then(modifiedPerson => {
          console.log('Modified person', modifiedPerson);
          
          setPersons(
            persons.map(person =>
              (person.id === duplicate.id)
              ? modifiedPerson
              : person
            )
          )
          setNewName('')
          setNewNumber('')
          showNotification(`Number for "${modifiedPerson.name}" updated!`)
        })
        .catch(error => {
          const response = error.response.data
          showNotification(response.error, true)
        })
      }
    }
    else {
      personService
      .add(newPerson)
      .then(person => {
        
        console.log('Person added:', person)
        setPersons(
          persons.concat(person)
        )
        setNewName('')
        setNewNumber('')
        showNotification(`${person.name} added!`)
      })
      .catch(error => {
        const response = error.response.data
        showNotification(response.error, true)
      })
    }
  }


  const handleNewFilter = (event) => {
    const filter = event.target.value
    setFilter(filter)
  }

  const handleNewName = (event) => {
    const name = event.target.value
    setNewName(name)
  }
  const handleNewNumber = (event) => {
    const number = event.target.value
    setNewNumber(number)
  }

  const removePerson = (personToRemove) => {
    personService.remove(personToRemove.id)
      .then(() => {
        console.log(personToRemove)
        setPersons(persons.filter(person => person.id !== personToRemove.id))
        showNotification(`${personToRemove.name} removed!`)
      })
      .catch(error => {
        showNotification(
          `${personToRemove.name} has already been removed from the server! :( `,
          true
        )
      })
  }

  return (
    <div>
      <Notification notification={notification} />
      <h2>Phonebook</h2>

      <Filter
        filter={filter}
        handleNewFilter={handleNewFilter}
      />

      <h3>Add a new</h3>
      <PersonForm
        addPerson={addPerson}
        newName={newName}
        newNumber={newNumber}
        handleNewName={handleNewName}
        handleNewNumber={handleNewNumber}
      />

      <h3>Numbers</h3>
      <Numbers
        persons={persons}
        filter={filter}
        removePerson={removePerson}
      />
    </div>
  )
}

const Filter = ({filter, handleNewFilter}) => (
  <div>
    filter shown with <input
      value={filter}
      onChange={handleNewFilter}
    />
  </div>
)

/* Omaan päähän ois tuntunut paljon fiksummalta siirtää name ja number -hookit tän sisään, mutta tien nyt ohjeen mukaan*/
const PersonForm = ({addPerson, newName, handleNewName, newNumber, handleNewNumber}) => (
  <form onSubmit={addPerson}>
    <div>name: <input
          value={newName}
          onChange={handleNewName}
        />
    </div>
    <div>
        number: <input
          value={newNumber}
          onChange={handleNewNumber}
        />
    </div>
    <div>
        <button type="submit">add</button>
    </div>
  </form>
)

const Numbers = ({persons, filter, removePerson}) => {
  return (
    <div>
      <ul>
        {persons
          .filter(p => {
            const regexp = new RegExp('^'+filter, 'i')
            return (regexp.test(p.name))
          })
          .map(
            p => (
              <li key={p.id}>
                {p.name} {p.number} <br />
                <button onClick={() => removePerson(p)}>Delete</button>
              </li>
            )
          )
        }
      </ul>
    </div>
  )
}

const Notification = ({notification}) => {
  if (!notification)
    return null

    const {message, isWarning} = notification

  const color = (isWarning) ? 'red' : 'green'
  const backgroundColor = (isWarning) ? 'rgb(255,200,200)' : 'lightgreen'

  const notificationStyle = {
    color,
    backgroundColor,
    fontSize: '20px',
    borderStyle: 'solid',
    borderRadius: '5px',
    padding: '10px',
    marginBottom: '10px'
  }

  return (
    <div style={notificationStyle}>
      {message}
    </div>
  )
}

export default App